@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
:root {
  --main-width: 600px;
  --small-screen-width: 90vw;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  color: #afacc3;
  scroll-behavior: smooth;
}

*:focus {
  outline: none;
}

body {
  background-color: #302c45;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

/* ScrollBar */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #2c2a38;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #575465;
  border-radius: 10px;
  -webkit-transition: background-color 0.4s;
  transition: background-color 0.4s;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #4e4b5a;
}

/* Spinner */
.loading {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.loader {
  -webkit-animation: spin 1.5s infinite; /* Safari */

  border: 4px solid #bdbdbd;
  border-radius: 50%;
  border-top: 4px solid #262b2f;
  width: 25px;
  height: 25px;
  animation: spin 1.5s infinite;
}

.fa-spinner {
  -webkit-animation: spin 1s infinite;
          animation: spin 1s infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Header ### Start ### */
#header {
  background-color: #37334a;
  border-bottom: 1px solid #2622368f;
  z-index: 5;
  position: relative;
}

#header .header-container {
  width: 600px;
  width: var(--main-width);
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0;
}

#header .logo h2 a {
  color: #0fa645;
  font-weight: 800;
}

#header ul {
  display: flex;
  align-items: center;
  list-style: none;
}

#header ul li {
  padding-left: 20px;
}

#header .dropdown-button,
.dropdown-menu {
  display: none;
}

.dropdown-menu {
  transition: transform 0.4s;
}

.logout:hover {
  cursor: pointer;
}

@media (max-width: 600px) {
  #header .header-container {
    width: 90vw;
    width: var(--small-screen-width);
  }

  #header .dropdown-button {
    display: block;
  }

  #header ul {
    display: none;
  }

  .dropdown-menu {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;

    transform: translateY(-200px);

    display: flex;
    flex-direction: column;
    background-color: #37334a;
    text-align: left;
    padding-top: 10px;
    align-items: flex-start;
    z-index: 4;
  }

  .dropdown-menu li {
    width: 100%;
    display: flex;
  }

  .dropdown-menu li a,
  .dropdown-menu li p {
    padding: 15px;
    width: 100%;
  }

  .dropdown-menu li a:hover,
  .dropdown-menu li p:hovers {
    background-color: #2622368f;
  }

  .bc {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #302c45ec;
    width: 100vw;
    height: 100vh;
    z-index: 2;
  }
}

/* Header ### End ### */

/* Search Form ### Start ### */
#search-form {
  background-color: #37334a;
  padding: 10px 0;
  border-bottom: 1px solid #2622368f;
}

#search-form form {
  display: flex;
  align-items: center;
  width: 600px;
  width: var(--main-width);
  margin: auto;
}

#search-form form .search-query {
  border: 1px solid #2622368f;
  width: 100%;
  display: flex;
  border-radius: 3px;
  position: relative;
}

#search-form form .search-query input {
  width: 100%;
  padding: 10px;
  background-color: #37334a;
  border: 0;
}

#search-form form .search-query img {
  width: 15px;
  margin-left: 15px;
}

#search-form form button {
  background-color: #12ad47;
  padding: 10px 25px;
  border: none;
  border-radius: 3px;
  color: #d6ffe8;
  margin-left: 20px;
  transition: background-color 0.2s;
}

#search-form form button:hover {
  cursor: pointer;
  background-color: #129b42;
}

.empty-input {
  position: absolute !important;
  padding: 5px;
  bottom: -35px;
  font-size: 0.8em;
  border-radius: 5px;
  -webkit-animation: shake 0.4s;
          animation: shake 0.4s;
  z-index: 3;
}

.empty-input p::before {
  content: "";
  position: absolute;
  top: -6px;
  background-color: #ad1212;
  width: 10px;
  height: 10px;
  padding: 5px;
  transform: rotate(45deg);
  z-index: -1;
}

.empty-input p {
  color: #fffffff0;

  position: relative;
  margin-top: 5px;
  background-color: #ad1212;
  padding: 5px;
  border-radius: 5px;
  z-index: 2;
}

.success {
  -webkit-animation: none !important;
          animation: none !important;
}

.success p {
  background-color: #0f943d !important;
}

.success p::before {
  background-color: #0f943d !important;
}

@-webkit-keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

@media (max-width: 700px) {
  #search-form form {
    width: 90vw;
    width: var(--small-screen-width);
  }
}

@media (max-width: 400px) {
  #search-form form {
    flex-direction: column;
  }

  #search-form form button {
    width: 100%;
    margin: 0;
    margin-top: 10px;
  }
}

/* Search Form ### End ### */

/* Search Results ### Start ### */
#search-results,
.job-table .jobs-wrapper {
  width: 600px;
  width: var(--main-width);
  margin: 50px auto 0 auto;
}

#search-results .results-header,
.job-table .jobs-wrapper .results-header {
  background-color: #332f46;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9em;
}

#search-results .jobs,
.job-table .jobs-wrapper .jobs {
  overflow-y: scroll;
  height: 60vh;
  background-color: #37334a;
  display: flex;
  flex-direction: column;
}

#search-results .jobs a,
.job-table .jobs-wrapper .jobs a {
  padding: 20px 15px;
  transition: background-color 0.4s;
}

#search-results .jobs a p,
.job-table .jobs-wrapper .jobs a {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

#search-results .jobs a:hover,
.job-table .jobs-wrapper .jobs a:hover {
  background-color: #302c45;
  cursor: pointer;
}

#search-results .jobs-no,
.job-table .jobs-wrapper .jobs-no {
  color: #5b5875;
}

@media (max-width: 600px) {
  #search-results,
  .job-table .jobs-wrapper {
    width: 90vw;
    width: var(--small-screen-width);
    margin: 10px auto 0 auto;
  }
}

/* Search Results ### End ### */

/* User Form ### Start */
.user-form form {
  width: 400px;
  margin: 50px auto;
}

.user-form form div {
  margin: 10px 0;
  position: relative;
}

.user-form form p {
  font-size: 0.8em;
}

.user-form form input {
  width: 100%;
  padding: 10px;
  background-color: #37334a;
  border: 0;
  border: 1px solid #2622368f;
  border-radius: 3px;
  /* font-size: 16px; */
}

.user-form form button {
  background-color: #12ad47;
  padding: 10px 25px;
  border: none;
  border-radius: 3px;
  color: #d6ffe8;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
}

.user-form form button:hover {
  cursor: pointer;
  background-color: #129b42;
}

.user-form form .submit .empty-input {
  bottom: -45px;
}

.user-form form .submit .loader {
  margin: 0;
  margin-right: 10px;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
  border-top: 2px solid #262b2f;
  width: 15px;
  height: 15px;
  -webkit-animation: spin 1.5s infinite;
          animation: spin 1.5s infinite;
}

@media (max-width: 600px) {
  .user-form form {
    width: 90vw;
    width: var(--small-screen-width);
  }
}

/* User Form ### End */

/* Update Password ### Start ### */
#update-password .password .wrapper,
#reset-password .password .wrapper {
  display: flex;
  align-items: center;
  margin: 0;
}

#update-password .password .wrapper div,
#reset-password .password .wrapper div {
  margin: 0 0 0 10px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#update-password .password .wrapper div img,
#reset-password .password .wrapper div img {
  width: 15px;
}

.show {
  background-color: #332f46;
  border-radius: 3px;
  border: 1px solid #5b5875;
  display: block;
}

.show:hover {
  cursor: pointer;
}

.job-table .jobs-wrapper .jobs li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.job-table .jobs-wrapper .jobs li a {
  width: 100%;
}

.job-table .jobs-wrapper .jobs li .bookmark {
  padding: 20px;
  transition: background-color 0.4s;
}

.job-table .jobs-wrapper .jobs li .bookmark:hover {
  background-color: #302c45;
}

.job-table .jobs-wrapper .jobs li .bookmarked {
  background-color: #0f943d;
  transition: background-color 0.4s;
}

.job-table .jobs-wrapper .jobs li .bookmarked:hover {
  background-color: #9b1212;
}

.job-table .jobs-wrapper .jobs li .bookmark:hover {
  cursor: pointer;
}

.job-table .jobs-wrapper .jobs li .bookmark img {
  width: 15px;
}

/* Update Password ### End ### */

